@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}
.header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 80px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header__icon{
    object-fit: contain;
    height: 30px;
    margin-left: 30px;
}

.header__center{
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border: 1px solid lightgray;
    border-radius: 999px;
}

.header__center > input{
    border: none;
    padding: 10px;
    outline-width: 0;
    /* width: 250px; */
}

.header__right{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 15vw;
    margin-right: 80px;
}
.home__section{
    display: -webkit-flex;
    display: flex;
    padding: 30px;
    /* margin-top: 20px; */
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.banner{
    /* background: url(bg7.jpg);
    background-position: 25% 65%; */
    background: url(/AirbnbCloneWebApp/static/media/bg11.d2443c64.png);
    background-position: 25% 90%;
    background-position-x: -300px;
    /* background-size: contain; */
    background-repeat: no-repeat;
    height: 50vh;
    width: 100%;
    position: relative;
    margin-bottom: 20px;
}

.banner__search{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.banner__searchButton{
    background-color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    color: #ff7779 !important;
}

.banner__info{
    background-color: black;
    color: white;
    padding-left: 50px; 
    padding-right: 50px;   
    padding-top: 20vh;
    padding-bottom: 40px;
    width: 300px;
}

.banner__info >  button{
    background-color: #ff7779;
    color: white;
    /* MUI make the text in upper case to make in into it original shape */
    text-transform: inherit;
    margin-top: 20px;
    font-weight: 500;
}

.banner__info >  button:hover{
    background-color: white;
    color: #ff7779;
}

.banner__info > h5{
    margin-top: 10px;
}
.search{
    position: absolute;
    top:35px;
    left: 25%;
    width: 100vw;
}

.search > h2{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 559px;
    padding: 10px;
    background-color: white;
    position: absolute;
    left: 0;
    /* top: 380px; */
    z-index: 4;
}

.search > input{
    width: 539px;
    padding: 20px;
    position: absolute;
    left: 0;
    height: 30px;
    top: 380px;
    border: none;
}

.search > input:focus {
    outline-width: 0;
}

.search > button{
    position: absolute;
    left: 0;
    top: 450px;
    text-transform: inherit !important;
    background-color: #ff7779;
    color: white;
    width: 579px;
}

.search > button:hover{
    background-color: white;
    color: #ff7779;
}
.card{
    margin:10px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);
    cursor: pointer;
    transition: -webkit-transform 100ms ease-in;
    transition: transform 100ms ease-in;
    transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
}

.card:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}

.card > img{
    width: 30vw;
    height: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    /* min-width: 300px;
    min-height: 200px;
    width: 100%; */
}

.card__info{
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    padding-top: 20px;
    border: 1;
}

.card__info > h2{
    font-size: 18px;
    font-weight: 600;
}

.card__info > h4{
    font-size: 14px;
    font-weight: 300;
    margin-top: 8px;
    margin-bottom: 8px;
}


